
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { NBC_SERVICE_ON_INTERNET_BANKING, RPT205000311_RES } from "@/shared/type/RPT202001SS02.type";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
import { ExportExcel } from "@/shared/services/ExportExcelService";

const excel = new ExportExcel()

export default defineComponent({
    name: 'RPT202001SS02',
    components: { Pagination, VueDatePicker },
    data() {
        return {
            approvalRequestDate: '' as unknown,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'Transaction Type' },
                { columnName: 'From Account No' },
                { columnName: 'Beneficiary Account Number' },
                { columnName: 'Beneficiary Account Name' },
                { columnName: 'Bank Name' },
                { columnName: 'Amount' },
                { columnName: 'Currency' },
                { columnName: 'Sender Fee' },
                { columnName: 'Receiver Fee' },
                { columnName: 'NCH Fee' },
                { columnName: 'Total Fee Charge' },
                { columnName: 'Remark' },
                { columnName: 'Status' },
                { columnName: 'Created by' },
                { columnName: 'Created date' },
                { columnName: 'Authorized by' },
                { columnName: 'Authorized Date' },
            ],
            table: [] as any,
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },
    
    mounted() {
        this.requestNBCServiceDetail()
    },

    computed: {
        formattedDate(): string {
            return this.approvalRequestDate ? this.$datetime.formatVueDatePicker(this.approvalRequestDate as Date) : ''
        },
    },

    methods: {
        async requestNBCServiceDetail() {
            const reqBody = {
                approvalRequestDate: this.approvalRequestDate ? this.$datetime.convertDateToString(this.approvalRequestDate as Date) : '',
                pageSize: this.pageSize,
                pageNumber: this.currentPage,
            }
            const res = await this.$request.getRequest("RPT20500311", reqBody, false) as RPT205000311_RES;
            this.table = res.body.list.map((item: NBC_SERVICE_ON_INTERNET_BANKING) => {
                return {
                    referenceNo: item.serverTransactionID,
                    bizCID: item.customerNo,
                    bizName: item.customerName,
                    transactionType: `${item.eBankTransactionTypeCode} : ${this.$codeConverter.convertValue("eBankTransactionTypeCode", item.eBankTransactionTypeCode)}`,
                    fromAccountNo: item.withdrawalAccountNo,
                    beneficiaryAccountNumber: item.receiverAccountNo,
                    beneficiaryAccountName: item.receiverName,
                    bankName: item.bankName,
                    amount: this.$format.formatCurrency(item.transactionAmount, item.transactionCurrencyCode),
                    currency: item.transactionCurrencyCode,
                    senderFee: this.$format.formatCurrency(item.senderFee, item.transactionCurrencyCode),
                    receiverFee: this.$format.formatCurrency(item.receiverFee, item.transactionCurrencyCode),
                    nCHFee: this.$format.formatCurrency(item.centerFee, item.transactionCurrencyCode),
                    totalFeeCharge: this.$format.formatCurrency(item.feeAmount, item.transactionCurrencyCode),
                    remark: item.withdrawalAccountRemark,
                    status: `${item.corpBankingApprovalStatusCode} : ${this.$codeConverter.convertValue("CorpBankingApprovalStatusCode", item.corpBankingApprovalStatusCode)}`,
                    createdBy: item.firstRegisterTellerID && item.firstRegisterTellerName ? `${item.firstRegisterTellerID} (${item.firstRegisterTellerName})` : '',
                    createdDate: item.firstRegisterDate && item.firstRegisterTime ? `${this.$datetime.formatStringToStringDate(item.firstRegisterDate)} ${this.$datetime.formatStringToTime(item.firstRegisterTime)}` : '',
                    authorizedBy: item.approverID && item.approverName ? `${item.approverID} (${item.approverName})` : '',
                    authorizedDate: item.authorizationDate && item.authorizationTime ? `${this.$datetime.formatStringToStringDate(item.authorizationDate)} ${this.$datetime.formatStringToTime(item.authorizationTime)}` : ''
                }
            })
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                'Reference No',
                'Biz CID',
                'Biz Name',
                'Transaction Type',
                'From Account No',
                'Beneficiary Account Number',
                'Beneficiary Account Name',
                'Bank Name',
                'Amount',
                'Currency',
                'Sender Fee',
                'Receiver Fee',
                'NCH Fee',
                'Total Fee Charge',
                'Remark',
                'Status',
                'Created by',
                'Created date',
                'Authorized by',
                'Authorized Date',
            ]

            const reqBody = {
                approvalRequestDate: this.approvalRequestDate ? this.$datetime.convertDateToString(this.approvalRequestDate as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20500311", reqBody, false) as RPT205000311_RES;

            const tableForExport = this.onMappingTableData(res.body.list)

			const dataForExport: ExportSheet = {
				data: tableForExport,
				columnHeader: [customHeader],
				sheetName: 'NBC Service on IB Report'
			}
			excel.exportSheet([dataForExport], 'NBC Service on Internet Banking Report')
        },

        onMappingTableData(lists) {
            return lists.map((item: NBC_SERVICE_ON_INTERNET_BANKING) => ({
                referenceNo: item.serverTransactionID,
                bizCID: item.customerNo,
                bizName: item.customerName,
                transactionType: `${item.eBankTransactionTypeCode} : ${this.$codeConverter.convertValue("eBankTransactionTypeCode", item.eBankTransactionTypeCode)}`,
                fromAccountNo: item.withdrawalAccountNo,
                beneficiaryAccountNumber: item.receiverAccountNo,
                beneficiaryAccountName: item.receiverName,
                bankName: item.bankName,
                amount: this.$format.formatCurrency(item.transactionAmount, item.transactionCurrencyCode),
                currency: item.transactionCurrencyCode,
                senderFee: this.$format.formatCurrency(item.senderFee, item.transactionCurrencyCode),
                receiverFee: this.$format.formatCurrency(item.receiverFee, item.transactionCurrencyCode),
                nCHFee: this.$format.formatCurrency(item.centerFee, item.transactionCurrencyCode),
                totalFeeCharge: this.$format.formatCurrency(item.feeAmount, item.transactionCurrencyCode),
                remark: item.withdrawalAccountRemark,
                status: `${item.corpBankingApprovalStatusCode} : ${this.$codeConverter.convertValue("CorpBankingApprovalStatusCode", item.corpBankingApprovalStatusCode)}`,
                createdBy: item.firstRegisterTellerID && item.firstRegisterTellerName ? `${item.firstRegisterTellerID} (${item.firstRegisterTellerName})` : '',
                createdDate: item.firstRegisterDate && item.firstRegisterTime ? `${this.$datetime.formatStringToStringDate(item.firstRegisterDate)} ${this.$datetime.formatStringToTime(item.firstRegisterTime)}` : '',
                authorizedBy: item.approverID && item.approverName ? `${item.approverID} (${item.approverName})` : '',
                authorizedDate: item.authorizationDate && item.authorizationTime ? `${this.$datetime.formatStringToStringDate(item.authorizationDate)} ${this.$datetime.formatStringToTime(item.authorizationTime)}` : ''
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.requestNBCServiceDetail();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1
                this.isResetPage = true;
                this.requestNBCServiceDetail()
            } else {
                this.isResetPage = true;
                this.requestNBCServiceDetail()
            }
        },

        handleReset() {
            this.approvalRequestDate = '' as unknown
            this.currentPage = 1
            this.isResetPage = true
            this.requestNBCServiceDetail()
        }
    }
})
